<template>
  <div style="text-align:center;">
    <h3 class="mdl-color-text--primary">Add Client</h3>
    <client-form ref="form">
    </client-form>
    <router-link
      tag="a"
      class="custom-button"
      :to="{ name: 'clients.list' }"
    >
     Back
    </router-link>
    <button
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary"
      @click="createClient">Add</button>

  </div>
</template>
<script>
import { ClientForm } from '.'
import { mapActions } from 'vuex'
import { MESSAGES } from '@/constants'

export default {
  components: {
    ClientForm
  },
  computed: {
    form: {
      get () {
        return this.$refs.form
      }
    }
  },
  methods: {
    ...mapActions([
      'addClient'
    ]),
    createClient () {
      this.form.valid().then(valid => {
        if (valid) {
          this.addClient(this.form.clientParams).then(() => {
            this.$toastr.s(MESSAGES.success_add)
            this.$router.push({ name: 'clients.list' })
          }).catch(e => {
            this.$toastr.e(e)
          })
        }
      })
    }
  }
}
</script>



